import React from "react";
import "./App.scss";
import D34, { useState } from "react";
import { useNavigate } from "react-router-dom";
import { createContext } from "react";
import {jsPDF} from "jspdf";
import html2canvas from "html2canvas";
export const ThemeContext = createContext(null);
function InputTask() {
  
  const [Task1, setTask1] = useState("");
  const [Time1, setTime1] = useState("");
  const [Task2, setTask2] = useState("");
  const [Time2, setTime2] = useState("");
  const [Task3, setTask3] = useState("");
  const [Time3, setTime3] = useState("");
  const [Task4, setTask4] = useState("");
  const [Time4, setTime4] = useState("");
  const [Task5, setTask5] = useState("");
  const [Time5, setTime5] = useState("");
//1
  function onChangeTask1(e) {
    setTask1(e.target.value);
  }

  function onChangeTime1(e) {
    setTime1(e.target.value);
  }
//2
  function onChangeTask2(e) {
    setTask2(e.target.value);
  }

  function onChangeTime2(e) {
    setTime2(e.target.value);
  }

   function onChangeTask3(e) {
    setTask3(e.target.value);
  }

  function onChangeTime3(e) {
    setTime3(e.target.value);
  }

  
  function onChangeTask4(e) {
    setTask4(e.target.value);
  }

  function onChangeTime4(e) {
    setTime4(e.target.value);
  }

  function onChangeTask5(e)
  {
    setTask5(e.target.value);
  }

  function onChangeTime5(e)
  {
    setTime5(e.target.value);
  }
  const [count, setCount] = useState(0);
  const [input, setInput] = useState("");
  function handleInput(e) {
    setInput(e.target.value);
    setCount((prev) => prev + 1);
  }


  if(count == 4){
    console.log("count is 4");
  }

  //submit onClick button
  function onSubmit(e) {
    localStorage.setItem("Task1", Task1);
    localStorage.setItem("Time1", Time1);
    localStorage.setItem("Task2", Task2);
    localStorage.setItem("Time2", Time2);
    localStorage.setItem("Task3", Task3);
    localStorage.setItem("Time3", Time3);
    localStorage.setItem("Task4", Task4);
    localStorage.setItem("Time4", Time4);
    localStorage.setItem("Task5", Task5);
    localStorage.setItem("Time5", Time5);
  }

  
    let navigate = useNavigate();
    function handleClick() {
      navigate('/schedule')
    }
    const [theme, setTheme] = useState("light");

    const toggleTheme = () => {
      setTheme((curr) => (curr === "light" ? "dark" : "light"));
    };

    if (theme === "light"){
   var idk =  "rgb(238, 236, 236)";
    }
    if (theme === "dark"){
      var idk = "#f5f5f5";
       }
 
  return (
    <ThemeContext.Provider value={{ theme, toggleTheme }}>

    <div className="InputTask"   id={theme}>
      <h1>Enter Task</h1>
      <h1 className="h1_tasks">Enter Tasks</h1>
      <div className="inputfields">
        <div className="inp1div">
          <input type="text" className="input1" id="inp1"  value={Task1} onChange={onChangeTask1} style={{fontSize : "20px", fontWeight:"800", fontFamily: "Open Sans" , caretColor:"rgba(190, 190, 190, 0.692"}}/>
          <input type="text" className="input1d" id="inp1d" placeholder="hours" value={Time1} onChange={onChangeTime1} style={{fontSize : "20px", fontWeight:"800", fontFamily: "Open Sans" , caretColor:"rgba(190, 190, 190, 0.692"}}/>
        </div>
        <div className="inp2div">
          <input type="text" className="input2" id="inp2" value={input} onInput={handleInput}  onChange={onChangeTask2} style={{fontSize : "20px", fontWeight:"800", fontFamily: "Open Sans" , caretColor:"rgba(190, 190, 190, 0.692"}}/>
          <input type="text" className="input2d" id="inp2d" placeholder="hours" value={Time2} onChange={onChangeTime2}style={{fontSize : "20px", fontWeight:"800", fontFamily: "Open Sans" , caretColor:"rgba(190, 190, 190, 0.692"}}/>
        </div>
        <div className="inp3div">
          <input type="text" className="input3" id="inp3" value={Task3} onChange={onChangeTask3} style={{fontSize : "20px", fontWeight:"800", fontFamily: "Open Sans" , caretColor:"rgba(190, 190, 190, 0.692"}}/>
        
          <input type="text" className="input3d" id="inp3d" placeholder="hours" value={Time3} onChange={onChangeTime3} style={{fontSize : "20px", fontWeight:"800", fontFamily: "Open Sans" , caretColor:"rgba(190, 190, 190, 0.692"}}/>
          
        </div>
        <div className="inp4div">
          <input type="text" className="input4" id="inp4"  value={Task4} onChange={onChangeTask4}style={{fontSize : "20px", fontWeight:"800", fontFamily: "Open Sans" , caretColor:"rgba(190, 190, 190, 0.692"}}/>
          <input type="text" className="input4d" id="inp4d" placeholder="hours" value={Time4} onChange={onChangeTime4}style={{fontSize : "20px", fontWeight:"800", fontFamily: "Open Sans" , caretColor:"rgba(190, 190, 190, 0.692"}}/>
        </div>
        <div className="inp5div">
          <input type="text" className="input5" id="inp5" value={Task5} onChange={onChangeTask5} style={{fontSize : "20px", fontWeight:"800", fontFamily: "Open Sans" , caretColor:"rgba(190, 190, 190, 0.692"}}/>
          <input type="text" className="input5d" id="inp5d" placeholder="hours" value={Time5} onChange={onChangeTime5} style={{fontSize : "20px", fontWeight:"800", fontFamily: "Open Sans" , caretColor:"rgba(190, 190, 190, 0.692"}}/>
     </div>
      </div>
      <div className="button_wrapper">
      <a 
      
       onClick={() => {
                  onSubmit();
                  handleClick();
                }}
             >
      
       Submit</a>
       </div>
       {/* <label> {theme === "light" ? "Light Mode" : "Dark Mode"}</label> */}
      
      <div className="theme_wrapper_wrapper">
       <div className="theme_wrapper">
          <input type="checkbox" id="toggle" className="toggle" onClick={toggleTheme}></input>
          </div>
          </div>
          <div className="background1" style={{backgroundColor: idk}}></div>
    </div>
     </ThemeContext.Provider>
  );
}

export default InputTask;
